let sizeItems = document.querySelectorAll(".size-item")
sizeItems.forEach((item => {
    let openBtn = item.querySelector(".size-item__btn")
    let closeBtn = item.querySelector(".size-item__close")

    closeBtn.onclick = function () {
        item.classList.remove("_show")
    }
    openBtn.onclick = function () {
        closeAllSizeDropdowns()
        item.classList.add("_show")
    }
    openBtn.onmouseover = function () {
        closeAllSizeDropdowns()
        item.classList.add("_show")
    }
    document.addEventListener( 'click', (e) => {
        const withinBoundaries = e.composedPath().includes(item);

        if ( ! withinBoundaries ) {
            item.classList.remove("_show")
        }
    })
}))

function closeAllSizeDropdowns() {
    sizeItems.forEach((item => {
        item.classList.remove("_show")
    }))
}
